@keyframes show {
  0% {
    margin-top: -100px;
  }
  5% {
    margin-top: -50px;
  }
  33% {
    margin-top: -50px;
  }
  38% {
    margin-top: -33px;
  }
  66% {
    margin-top: -33px;
